.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  height: 100%;
}

.clockface {
  margin: 50px 25px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 25px;
}

.cell {
  margin: 5px;

  width: 40px;
  height: 80px;
  position: relative;
}

.cell > div {
  margin: 0;
  display: block;
  position: absolute;
  /*background: #000;*/
  opacity: 0.02;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: opacity .4s ease-in-out;
  -o-transition: opacity .4s ease-in-out;
  transition: opacity .4s ease-in-out;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.cell > div.active {
  opacity: .8;
  -webkit-animation: op .2s ease-in-out;
          animation: op .2s ease-in-out;
}

.cell > div:nth-child(1) {
  width: 36px;
  height: 0px;
  top: 0;
  left: 2px;

  border-top: 6px solid #000;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
}

.cell > div:nth-child(2) {
  height: 36px;
  top: 2px;
  left: 0;

  border-left: 6px solid #000;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
}
.cell > div:nth-child(3) {
  height: 36px;
  top: 2px;
  right: 0;

  border-right: 6px solid #000;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
}
.cell > div:nth-child(4) {
  width: 28px;
  top: 36px;
  left: 6px;
  height: 8px;
  background: #000;
}

.cell > div:nth-child(4):before {
  content:"";
  position: absolute;
  left: -4px;
  top: 0;
  width: 0;
  height: 0;
  border-top: 4px solid transparent;
  border-right: 4px solid #000;
  border-bottom: 4px solid transparent;
}
.cell > div:nth-child(4):after {
  content:"";
  position: absolute;
  right: -4px;
  top: 0;
  width: 0;
  height: 0;
  border-top: 4px solid transparent;
  border-left: 4px solid #000;
  border-bottom: 4px solid transparent;
}

.cell > div:nth-child(5) {
  height: 36px;
  bottom: 2px;
  left: 0;

  border-left: 6px solid #000;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
}
.cell > div:nth-child(6) {
  height: 36px;
  bottom: 2px;
  right: 0;

  border-right: 6px solid #000;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
}
.cell > div:nth-child(7) {
  width: 36px;
  bottom: 0;
  left: 2px;

  border-bottom: 6px solid #000;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
}

@-webkit-keyframes op {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes op {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}


